.home {

    text-align: center;
    padding-top: 200px;
    font-size: 30px;
}
.home-text{
    justify-content: center;
}

h1 {
    animation: typingEffectAnimation 1.5s steps(30, end);
}

@keyframes typingEffectAnimation {
    from {
        width: 0;
    }

    to {
        width: 100%
    }

}
button{
    margin-top: 30px;
    padding: 10px 20px;
    font-weight: 20px;
    color:violet;
    border-radius: 4px;
    cursor: pointer;
    font-size: 2 rem;
    border: 2px solid violet;
    background: transparent;
    
    
}