.nvbr{
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2 rem;
    margin-left: 600px;
}
.nvbr-menu{
    display: flex;
    text-align: center;
    list-style: none;
}
.nvbr-menu-item{
    line-height: 40px;
    margin-right: 1.2rem;
    font-size: 1.5rem;
}
.nvbr-menu-item::after{
content: '';
display: block;
height:3px;
width:0;
background: transparent;
transition: all 0.5s ease;
}
.nvbr-menu-item:hover::after{
    width:100%;
    background: violet;
}
.nvbr-menu-item-links{
    color: violet;
    text-decoration: none;
    padding: 0.5rem;
}