.wrpper{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 40px;
    margin-bottom: 30px;
}
.inputName,.inputEmail,.inputDescription{
    width:100%;
    background: transparent;
    padding: 16px 12px;
    border: 2px solid violet;
    transition: all 0.5s ease;
    position: relative;
    color: white;
}
h3{
    font-size: 30px;
    margin-left: 30px;
}

.nameLabel{
    font-weight: 700;
    position: absolute;
    top: 260px;
    left: 30px;
    color: white;
}
.emailLabel{
    font-weight: 700;
    position: absolute;
    top: 260px;
    left: 680px;
    color: white;

}
.descriptionLabel{
    font-weight: 700;
    position: absolute;
    top: 350px;
    left: 30px;
    color: white;

}
button{
    margin-left: 30px;
    padding: 10px 30px;
}